*{
  font-family: 'Poppins', sans-serif;
}
body{
  margin: 0px;
  margin: 0px;
}
#description{
    height: 100px;
}

#nestedDiv {
  float: left;
  width: 25%;
  padding-top: 5%;
  background-color: white;
  height: 100vh;
  text-align: center;
}
#workplace {
  float: right;
  width: 75%;
  background-color: #f2f2f2;
  height: 100vh;
}
@media (max-width: 992px) {
  #nestedDiv {
    width: 100%;
    height: 50px;
    display: flex;
    padding-top: 0%;
    justify-content: center;
    align-items: center;
  }
  #workplace {
    width: 100%;
    height: 100vh;
  }
}

@media (max-width: 700px) {
    #nestedDiv {
      width: 100%;
      height: 50px;
      display: flex;
      padding-top: 0%;
      font-size: 10px;
    }
}

@media (max-width: 400px) {
    #nestedDiv {
      width: 100%;
      height: 50px;
      display: flex;
      padding-top: 0%;
      font-size: 7px;
    }
}
#footerSuperDiv{
    background-image: url(/static/media/Marketing.04c90c7b.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: auto;
}
#footerSubDiv1{
    background-color: rgba(0, 10, 31, 0.897);
    height: auto;
    text-align: center;
    padding-bottom: 10%;
}
#bannerContainer{
    transition: 1s;
}
#body{
    background-color: #f8fdff;
}
@media (min-width: 992px) {
    #bannerContainer{
        display: flex;
    }
    #sectionSectionDiv{
        display: flex;
    }
    #reviewSectionDiv{
        display: flex;
    }
    .bannerDivs{
        width: 50%;
    }
    .serviesDivs{
        width: 50%;
    }
    .reviewDivs{
        width: 50%;
    }
    #bannerParagraph{
        font-size: 40px!important;
    }
}
#serviesCard {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  transition: 0.5s;
}

#serviesCard:hover {
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0px 2px 5px rgb(204, 204, 204);
  /* color: #ffbc02; */
}

#projectCard {
  -webkit-filter: grayscale(50%);
  filter: grayscale(50%);
  transition: .5s;
}

#projectCard:hover {
    -webkit-filter: grayscale(0%);
    filter: grayscale(0%);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0px 2px 5px rgb(204, 204, 204);
  }
